/* Стили для скрытых полей */
.hidden-field {
  display: none !important;
}

/* Стили для нашего поля datetime */
#billing-saphali-datetime {
  width: 100%;
  max-width: 300px;
  padding: 8px;
  margin-top: 5px;
}

/* Стилизация страницы "Спасибо за заказ" */
.saphali-datetime-thankyou,
.saphali-datetime-admin {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  padding: 15px;
  margin-top: 20px;
}

/* Обеспечиваем, что Datepicker отображается поверх других элементов */
.ui-datepicker {
  z-index: 10000 !important;
}

.ui-timepicker-container {
  z-index: 10000 !important;
}

/* Дополнительные стили для DateTimePicker (если необходимо) */
